import React, { useState, useEffect } from "react";

export default (props) => {
  // i don't wanna lift this property because it will
  // re-render the whole app every second and I fell uncomfortable with that
  const [time, setTime] = useState(0);
  const [timer, setTimer] = useState();
  const [rank, setRank] = useState(
    JSON.parse(localStorage.getItem("bestRank")) || {
      time: undefined,
      rolles: undefined,
    }
  );
  if (props.tenzies) clearInterval(timer);

  useEffect(() => {
    setTime(0);
    if (props.fresh == false) {
      setTimer(setInterval(() => setTime((s) => ++s), 1000));
    } else {
      clearInterval(timer);
    }
  }, [props.fresh]);
  useEffect(() => {
    if (props.tenzies) {
      const rolles = props.rolles;
      const data = {
        time: rank.time < time && rank.time != undefined ? rank.time : time,
        rolles:
          rank.rolles < rolles && rank.rolles != undefined
            ? rank.rolles
            : rolles,
      };
      localStorage.setItem("bestRank", JSON.stringify(data));
    }
  }, [props.tenzies]);

  let minutes = Math.floor(time / 60);
  let seconds = Math.floor((time / 60 - minutes) * 60);
  return (
    <div className="statistic">
      <span className="rolles">Rolles: {props.rolles}</span>|
      <span className="time">
        Time: {minutes} : {seconds}
      </span>
      |
      <span className="ranking">
        ?
        <span className="best-rank">
          Higher Rank
          <br />
          Time : {rank.time}
          <br />
          Rolles : {rank.rolles}
        </span>
      </span>
    </div>
  );
};
