import React from "react";
import "../sass/dice.sass";
export default function Die(props) {
  const classes = ["0", "one", "two", "three", "four", "five", "six"];
    
  return (
    <div className="per-container" onClick={props.hold}>
      <div
        className={`die ${classes[props.value]} ${
          props.isHeld ? "active" : ""
        }`}
      >
        <span className="die-face one"></span>
        <span className="die-face two"></span>
        <span className="die-face three"></span>
        <span className="die-face four"></span>
        <span className="die-face five"></span>
        <span className="die-face six"></span>
      </div>
    </div>
  );
}
