"use strict";
import React, { useEffect, useRef, useState } from "react";
import Die from "./components/Die";
import Statistic from "./components/Statistics";
import Confetti from "react-confetti";
import { nanoid } from "nanoid";
import "./sass/style.scss";

export default function App() {
  const [dice, setDice] = useState(allNewDice());
  const [tenzies, setTenzies] = useState(false);
  const [rolles, setRolles] = useState(0);
  const oneIsHeld = dice.find((die) => die.isHeld);
  const fresh = dice.every((die ) => die.isHeld === false);
  function rollDice() {
    if (tenzies) {
      setDice((prevDice) => allNewDice());
      setTenzies(false);
      setRolles(0);
      return;
    }

    setDice((prevDice) => {
      // i have change only the value to avoid re-render...
      // -entire new object aka Elements and that will break animations
      return prevDice.map((item) =>
        item.isHeld ? item : { ...item, value: randomNum() }
      );
    });
    setRolles((preRolles) => ++preRolles);
  }

  function holdDice(id) {
    setDice((prevDice) =>
      prevDice.map((die) =>
        die.id === id ? { ...die, isHeld: !die.isHeld } : die
      )
    );
  }
  useEffect(() => {
    const allHeld = dice.every((die) => die.isHeld === true);
    const allTheSame = dice.every((die) => die.value === dice[0].value);
    if (allHeld && allTheSame) setTenzies(true);
  }, [dice]);

  const diceElements = dice.map((die, index) => (
    <Die
      isHeld={die.isHeld}
      key={die.id}
      value={die.value}
      hold={() => holdDice(die.id)}
    />
  ));
  return (
    <main>
      {tenzies && <Confetti />}
      <h1 className="title">Tenzies</h1>
      <p className="instructions">
        Roll until all dice are the same. Click each die to freeze it at its
        current value between rolls.
      </p>
      <div className="dice-container">{diceElements}</div>
      <button className="roll-dice" onClick={rollDice}>
        {tenzies ? "New Game" : "Roll"}
      </button>
      <Statistic
        fresh={fresh}
        oneIsHeld={oneIsHeld}
        rolles={rolles}
        tenzies={tenzies}
      />
    </main>
  );
}

function allNewDice() {
  const newDices = [];
  for (let i = 0; i < 10; i++) {
    newDices.push({
      value: randomNum(),
      isHeld: false,
      id: nanoid(),
    });
  }
  return newDices;
}
function randomNum() {
  return Math.ceil(Math.random() * 6);
}

function winDir() {
  const newDices = [];
  for (let i = 0; i < 10; i++) {
    newDices.push({
      value: 1,
      isHeld: true,
      id: nanoid(),
    });
  }
  newDices[0].isHeld = false;
  return newDices;
}
